<template>
	<div class="explosion">		
		<div class="proImg" :class="imgClass">
			<img src="../assets/img/fruit-orange.png" alt="商品图片">
		</div>
		<div class="pro-discript" :class="txtClass">
			<div class="big-title">新鲜的进口水果</div>
			<div class="second-title">新鲜的进口水果（100g）</div>
			<ul class="discript-ul">
				<li>*及第三方客户</li>
				<li>*及第三方客户</li>
				<li>*及第三方客户</li>
			</ul>
			<div class="buy-wrap">
				<div class="p-wrap">￥<b class="price">10</b>.2</div>
				<button class="buyNow">立即购买</button>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
		    return{
		     
		    }
		  },
		props:["imgClass","txtClass"],
		methods:{
			addClass:function(){
				
			}
		},
		created(){
			// this.addClass();
		}
	}
</script>

<style scoped>
.explosion {
	border: 1px solid #c48540;
	margin: 0 6px;
	border-radius: 8px;
	padding: 10px;
	position: relative;
	overflow: hidden;
}
.proImg{
	position: absolute;
	width: 50%;
	height: auto;	
	bottom: 6px;
	z-index: -1;
}
.proImg img{
	width: 100%;
}
.pro-discript {    
	overflow: hidden;
}
.ex-img-left{
	left: 0;
}
.ex-img-right{
	right: 0;
}
.ex-dsc-right{
	float: right;
}
.ex-dsc-left{
	float: left;
}
.big-title {
	font-size: 1.8em;
	font-weight: bold;
	color: #ddb075;
}
.second-title {
	font-weight: bold;
	color: #9f9d9b;
	text-align: center;
}
.discript-ul {
	width: 70%;   
	padding: 0.5em 0;
	border-bottom: 1px solid #c48540;
	border-top: 1px solid #c48540;
}
.ex-dsc-right .discript-ul{
	margin: 1em 0 1em 20%;
}
.ex-dsc-left .discript-ul{
	margin: 1em 0;
}
.discript-ul li {
	color: #9f9d9b;
	line-height: 1.3em;
	padding-left: 0.8em;
}
.buy-wrap {    
	position: relative;
}
.ex-dsc-left .buy-wrap{
	padding-left: 0;
}
.ex-dsc-right .buy-wrap{
	padding-left: 2em;
}
.p-wrap {
	color: #b7681e;
}
.price {
	font-size: 3em;
	font-weight: bold;
}
.buyNow {
	background-color: #be772c;
	border: 1px solid #be772c;
	color: #fff;
	padding: 5px 10px;
	border-radius: 1em;
	position: absolute;
	bottom: 10px;

}
.ex-dsc-left .buyNow{
	left: 50%;
}
.ex-dsc-right .buyNow{
	left: auto;
	right:0;
}
</style>


