<template>
	<div class="coupon">		
		<div class="c-top">
			<div class="ct-4 pull-left">
				<b class="m-value">5</b>
			</div>			
			<div class="ct-6 pull-left">
				<div class="rmb">RMB</div>
				<!-- <div class="text">优惠券</div> -->
			</div>			
		</div>
		<div class="getBtn">立即领取</div>
		<div class="shadow"></div>
	</div>
</template>
<script>
	export default{
		props:["message"],
		methods:{
			
		}
	}
</script>
<style scoped>
.coupon {
	width: 5em;
	height: 5em;
	background-color: #90ba81;
	border-radius: 2.5em;
	position: relative;
	margin: 0 auto;
}
.c-top {
	clear: both;
	overflow: hidden;
	margin: 0 auto;
}
.ct-4 {
	width: 50%;
    margin-top: 0.2em;
    text-align: right;
}
.ct-6 {
	width: 50%;
}
.m-value {
	color: #fff;
	font-size: 2em;
	text-align: right;
	margin-right: 0.1em;
}
.rmb {
	color: #fff;
	font-weight: bold;
	    font-size: 0.8em;	   
	    line-height: 5em; 
}
.text {
	font-size: 0.3em;
	font-weight: bold;
}
.getBtn {
	width: 5em;
    background-color: #fff;
    font-size: 0.6em;
    border-radius: 5px;
    margin: 0 auto;
    text-align: center;
    position: absolute;
    bottom: 20px;
    left: 50%;
    margin-left: -2.5em;
}
.shadow {
	width: 100%;
	height: 15px;
	background-color: #fff;
	position: absolute;
	bottom: 0;
	left: 0;
	border-top: 1px solid #e4e4e4;
}
</style>


