<template>
	<div class="nav-menu">
		<div class="menu-wrap">
			<div class="menu-text">{{menuTxt}}</div>
		</div>
	</div>
</template>

<script>
	export default{
		props:["menuTxt"],
		methods:{
			sendMsgToParent:function(){
				this.$emit("listenToChildEvent","this message is from child");
			}
		}
	}
</script>

<style scoped>
	.nav-menu {
    width: 70%;
    margin: 0 auto;    
    padding: 5px 0;
    background: -webkit-linear-gradient(#192713, #334122);
    background: -moz-linear-gradient(#192713, #334122);
    background: -ms-linear-gradient( #192713, #334122);
	background: -o-linear-gradient(#192713, #334122);
	background: linear-gradient(#192713, #334122);
	/*background-image: url('../assets/img/c.png');*/
}
.menu-wrap {
    border: 1px solid #ddd;
    margin: 0 5px;
}
.menu-text {
    text-align: center;
    color: #fff;
    padding: 10px 0;
    width: 1em;
    margin: 0 auto;
}
</style>


